<!-- 信维——当机推移看板 -->
<template>
  <div class="kanban">
    <!-- <div class="titleHead">
      <p id="pHead"></p>
      <p id="time">{{ time }}</p>
    </div> -->
    <search-bar @search="search" :searchData="searchData" :searchOptions="searchOptions" @getGroupSucceed="loadData"></search-bar>
    <div class="kanbanContent">
      <div class="top_content">
        <card-module title="产量推移图" cardBg="cardBg_large" titleBg="cardTitle_large" width="100%" height="100%"
          titleWidth="70%" @exportExcel="exportEchart">
          <div style="width: 100%; height: 100%" ref="performance"></div>
        </card-module>
      </div>
      <div class="bottom_content">
        <card-module title="产能报表" cardBg="cardBg_large" titleBg="cardTitle_large" width="100%" height="100%"
          titleWidth="80%" @exportExcel="exportTable">
          <VXETable ref="xGrid" :tableName="''" :height="$options.filters.getTableHeight(160) / 2 - 20"
            :tableLoading="mainPage.tableLoading" :tableDatas="mainPage.data" :tableColumns="mainPage.columns"
            :highlightCurrentRow="true" :tablePage="mainPage.page" @sortChange="sortChange"
            @onTablePageChange="handleMainPageChange">
          </VXETable>
        </card-module>
      </div>
    </div>
  </div>
</template>

<script>
// import $ from 'jquery';
// import vueSeamlessScroll from 'vue-seamless-scroll';
import $ from 'jquery';
import VXETable from '@/components/common/Table';
import { fontSize, parseTime } from '@/utils/common';
import { KANBAN_CONFIG } from '@/utils/ajaxConfigure';
import CardModule from '@/components/common/kanban/CardModule';
import searchBar from './components/searchBar.vue';
import { exportJson2Excel } from '@/utils/exportExcel';
import { formatJson } from '@/utils/publicFunction';
import { prodChartQty, deviceQtyTable } from '@/api/xwReport';
import { BTN_TEXT } from '@/utils/constantsFile';
import { getSearchToday } from '@/utils/publicFunction2';
export default {
  name: 'kanban_xw_currentCapacity',
  components: {
    // vueSeamlessScroll,
    VXETable,
    CardModule,
    searchBar
  },
  data() {
    return {
      title: '',
      BTN_TEXT: BTN_TEXT,
      groupId: 0,
      time: '',
      dataTime: '',
      searchData: {
        groupValue: [1, 2, 3, '00000190_00000005'],
        date: [new Date(getSearchToday(6) + ' 00:00:00'), new Date(getSearchToday(0) + ' 23:59:59')],
        timeKeyEnum: 'DAY'
      },
      searchOptions: [
        {
          type: 'cascader',
          key: 'groupValue'
        },
        {
          type: 'daterange',
          key: 'date',
          options: [
            {
              label: '小时',
              value: 'LESSON'
            },
            {
              label: '天',
              value: 'DAY'
            },
            {
              label: '年',
              value: 'MONTH'
            }
          ]
        }
      ],
      kanbanConfig: { title: '安灯看板', key: '', time: 15000 },
      stateEnum: {
        yellow_off: '停用',
        red_e: '异常',
        blue_m: '保养',
        green_w: '待料',
        white_m: '维护',
        green_p: '生产'
      },
      fontStateEnum: {
        停用: 'yellow_off_font',
        异常: 'red_e_font',
        保养: 'blue_m_font',
        待料: 'green_w_font',
        维护: 'white_m_font',
        生产: 'green_p_font'
      },
      deviceList: [],
      coatingData: {},
      barChartInfo: {
        current: 1,
        size: 4
      },
      maintainAnalyse: {
        current: 1,
        size: 4
      },
      timer: null,
      timer2: null,
      mainPage: {
        show: true,
        page: {
          total: 0,
          currentPage: 1,
          pageSize: 10,
          align: 'right',
          pageSizes: [10, 20, 50, 100, 200, 500],
          layouts: ['Sizes', 'PrevPage', 'Number', 'NextPage', 'Total'],
          perfect: true
        },
        data: [],
        columns: [
          {
            field: 'workshop',
            title: '车间',
            width: 'auto',
            align: 'center'
          },

          {
            field: 'productionLine',
            title: '产线',
            width: 'auto',
            align: 'center'
          },
          {
            field: 'deviceName',
            title: '设备',
            width: 'auto',
            align: 'center'
          },
          {
            field: 'actualQty',
            title: '生产数',
            width: 'auto',
            align: 'center'
          },
          {
            field: 'okQty',
            title: 'OK数',
            width: 'auto',
            align: 'center'
          },
          {
            field: 'ngQty',
            title: 'NG数',
            width: 'auto',
            align: 'center',
            formatter: ({ cellValue }) => {
              if (cellValue) {
                return cellValue;
              } else {
                return '-';
              }
            }
          },
          {
            field: 'yield',
            title: '良率',
            width: 'auto',
            align: 'center',
            formatter: ({ cellValue }) => {
              return Number(cellValue).toFixed(2);
            }
          },
          {
            field: 'availability',
            title: '稼动率',
            width: 'auto',
            align: 'center',
            formatter: ({ cellValue }) => {
              if (cellValue) {
                return Number(cellValue).toFixed(2);
              } else {
                return '-';
              }
            }
          }
        ],
        tableLoading: false,
        XData: [],
        actualQty: [],
        planQty: []
      }
    };
  },
  mounted() {
    document.documentElement.style.fontSize = this.fontSize() + 'px';
    window.onresize = () => {
      document.documentElement.style.fontSize = this.fontSize() + 'px';
    };
    // this.loadData();
    // this.timer = setInterval(() => {
    //   this.loadData();
    // }, this.kanbanConfig.time || 3000);
  },
  created() {
    var that = this;
    if (KANBAN_CONFIG[this.getUrlKeyMain('cid', window.location.href)]) {
      that.kanbanConfig =
        KANBAN_CONFIG[this.getUrlKeyMain('cid', window.location.href)];
    }
    this.getDataTime();
  },
  destroyed() {
    this.timer && setInterval(this.timer);
    this.timer2 && setInterval(this.timer2);
  },
  methods: {
    exportEchart() {
      const multiHeader = [];
      const header = ['', ...this.XData];
      const data = [
        ['实际产量', ...this.actualQty],
        ['计划产量', ...this.planQty]
      ];
      exportJson2Excel({
        multiHeader,
        header,
        data,
        filename: '产量推移图',
        merges: []
      });
    },
    exportTable() {
      // const showArr = this.mainPage.columns.map(item => item.field);
      const multiHeader = [];
      const header = this.mainPage.columns.map(item => item.title);
      const filterVal = this.mainPage.columns.map(item => item.field);
      const data = formatJson(filterVal, this.mainPage.data);
      exportJson2Excel({
        multiHeader,
        header,
        data,
        filename: '产能报表',
        merges: []
      });
      // this.$refs.xGrid.exportData('测试', this.mainPage.data, showArr);
    },
    async getProdChartData() {
      try {
        const { timeKeyEnum, date, groupValue } = this.searchData;
        const param = {
          timeKeyEnum
        };
        if (date) {
          if (timeKeyEnum === 'MONTH') {
            param.startTime = parseTime(date);
            param.endTime = parseTime(date);
          } else if (timeKeyEnum === 'LESSON') {
            param.startTime = parseTime(date[0]);
            param.endTime = parseTime(date[1]);
          } else if (timeKeyEnum === 'DAY') {
            const newDate = date[1];
            newDate.setHours(23);
            newDate.setMinutes(59);
            newDate.setSeconds(59);
            param.startTime = parseTime(date[0]);
            param.endTime = parseTime(newDate);
          }
        } else {
          param.timeKeyEnum = 'DAY';
          param.startTime = getSearchToday(6) + ' 00:00:00';
          param.endTime = getSearchToday(0) + ' 23:59:59';
        };
        if (groupValue) {
          var str = groupValue[groupValue.length - 1];
          if (isNaN(str)) {
            param.deviceCode = str;
          } else {
            param.groupId = str;
          }
        };
        const res = await prodChartQty(param, {});
        this.XData = res.rows.map(item => item.key);
        this.actualQty = res.rows.map(item => item.actualQty);
        this.planQty = res.rows.map(item => item.planQty);
        this.drawPerformance(this.XData, this.actualQty, this.planQty);
      } catch (error) {
        console.log(error);
      }
    },
    // 设备产能表格
    async getProdTableData() {
      try {
        const { timeKeyEnum, date, groupValue } = this.searchData;
        const param = {
          current: this.mainPage.page.currentPage,
          size: this.mainPage.page.pageSize,
          timeKeyEnum
        };
        if (date) {
          if (timeKeyEnum === 'MONTH') {
            param.startTime = parseTime(date);
            param.endTime = parseTime(date);
          } else if (timeKeyEnum === 'LESSON') {
            param.startTime = parseTime(date[0]);
            param.endTime = parseTime(date[1]);
          } else if (timeKeyEnum === 'DAY') {
            const newDate = date[1];
            newDate.setHours(23);
            newDate.setMinutes(59);
            newDate.setSeconds(59);
            param.startTime = parseTime(date[0]);
            param.endTime = parseTime(newDate);
          }
        } else {
          param.timeKeyEnum = 'DAY';
          param.startTime = getSearchToday(6) + ' 00:00:00';
          param.endTime = getSearchToday(0) + ' 23:59:59';
        };
        if (groupValue) {
          var str = groupValue[groupValue.length - 1];
          if (isNaN(str)) {
            param.deviceCode = str;
          } else {
            param.groupId = str;
          }
        };
        const res = await deviceQtyTable(param, {});
        this.mainPage.page.total = res.total;
        this.mainPage.data = res.rows;
      } catch (error) {
        console.log(error);
      }
    },
    sortChange(property, order) {
      this.orderByData = {};
      this.orderByData[property] = order;
      this.handleMainPageChange(1, this.mainPage.page.pageSize);
    },
    handleMainPageChange(currentPage, pageSize) {
      this.mainPage.page.currentPage = currentPage;
      this.mainPage.page.pageSize = pageSize;
      this.getProdTableData();
    },
    getDataTime() {
      this.dataTime = new Date(
        $.ajax({ type: 'HEAD', async: false }).getResponseHeader('Date')
      ).getTime();
      this.toGetTime();
    },
    toGetTime() {
      setInterval(() => {
        var date = new Date(this.dataTime);
        var y = date.getFullYear();
        var M =
          date.getMonth() < 9
            ? '0' + (date.getMonth() + 1)
            : date.getMonth() + 1;
        var d = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
        var h = date.getHours() < 10 ? '0' + date.getHours() : date.getHours();
        var m =
          date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes();
        var s =
          date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds();
        this.time =
          y + ' 年 ' + M + ' 月 ' + d + ' 日   ' + h + ':' + m + ':' + s;
        this.dataTime += 1000;
      }, 1000);
    },
    fontSize(num = 10) {
      const clientWidth =
        window.innerWidth ||
        document.documentElement.clientWidth ||
        document.body.clientWidth;
      if (!clientWidth) return;
      const fontSize = clientWidth / 1920;
      return num * fontSize;
    },
    showLogo() {
      return 'logo_' + this.kanbanConfig.logo;
    },
    getUrlKeyMain(name, url) {
      // eslint-disable-next-line no-sparse-arrays
      return (
        decodeURIComponent(
          (new RegExp('[?|&]' + name + '=' + '([^&;]+?)(&|#|;|$)').exec(
            url
          ) || ['', ''])[1].replace(/\+/g, '%20')
        ) || null
      );
    },
    drawPerformance(xData, actualQty, planQty) {
      const Performance = this.$echarts.init(this.$refs.performance);
      const option = {
        // color: EQUIPMENT_STATUS_REPORT.COLOR,
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            // 坐标轴指示器，坐标轴触发有效
            type: 'shadow' // 默认为直线，可选为：'line' | 'shadow'
          }
        },
        legend: {
          show: true,
          right: '5%',
          textStyle: {
            color: '#ffffff'
          }
        },
        // barMaxWidth: '60%',
        grid: {
          top: '12%',
          left: '3%',
          right: '5%',
          bottom: '8%',
          containLabel: true
        },
        xAxis: [
          {
            type: 'category',
            name: '日期',
            data: xData,
            axisTick: {
              alignWithLabel: true
            },
            axisLine: {
              lineStyle: {
                color: '#929EE6'
              }
            },
            axisLabel: {
              fontSize: fontSize(14),
              color: '#D8D8D8'
            },
            nameTextStyle: {
              fontSize: fontSize(16)
            },
            nameGap: '5'
          }
        ],
        yAxis: [
          {
            type: 'value',
            name: '产量',
            // minInterval: 1,
            // splitNumber: 0.2,
            axisLine: {
              lineStyle: {
                color: '#929EE6'
              }
            },
            axisLabel: {
              fontSize: fontSize(14),
              color: '#D8D8D8'
            },
            nameTextStyle: {
              fontSize: fontSize(16)
            },
            splitLine: {
              show: false
            }
          }
        ],
        series: [
          {
            type: 'bar',
            name: '实际产量',
            barWidth: '50%',
            itemStyle: {
              color: '#009092'
            },
            label: {
              show: false,
              position: 'inside',
              color: '#ffffff'
            },
            data: actualQty
          },
          {
            type: 'line',
            name: '计划产量',
            symbol: 'circle',
            symbolSize: 10,
            // barWidth: '35%',
            itemStyle: {
              color: '#70bf41'
            },
            lineStyle: {
              width: 3
            },
            data: planQty
          }
        ]
      };
      Performance.setOption(option, true);
      window.addEventListener('resize', () => {
        Performance.resize();
      });
    },
    search() {
      this.mainPage.page.currentPage = 1;
      this.mainPage.page.pageSize = 10;
      this.getProdChartData();
      this.getProdTableData();
    },
    loadData() {
      this.getProdChartData();
      this.getProdTableData();
    }
  }
};
</script>

